@import "/src/global.scss";

.text {
  position: relative;
  top: 25%;
  text-align: left;

  .header {
    padding: 12px 0px;

    font-size: 35px;
    font-weight: 600;
  }

  .tags {
    font-size: 16px;
    font-weight: 300;
    color: $paleFontColor;
  }

  .description {
    font-size: 20px;
    padding: 10px 0px;
    inline-size: 85%;
    overflow: wrap;
    font-weight: 350;
    line-height: 150%;
  }
}

@media (max-width: 970px) {
  .text {
    text-align: center;
    top: 5%;

    .header {
      font-size: 26px;
    }

    .tags {
      padding-top: 20px;
      font-size: 12px;
    }

    .description {
      font-size: 16px;
    }

    .description {
      inline-size: auto;
      margin-left: 8%;
      margin-right: 8%;
    }
  }
}